
import fondo from "../images/DigimonReDigitize.jpg";
import '../css/akiho.css';
import akiho from "../images/akiho.jpg";
import akiho1 from "../images/akiho1.jpeg";
import akiho2 from "../images/akiho2.jpg";
import akiho3 from "../images/akiho3.jpg"
import PiyomonVpet from "../images/PiyomonVpet.png";
import PiyomonVpet2 from "../images/PiyomonVpet2.png";
import BirdramonVpet from "../images/BirdramonVpet.png";
import garudamonVpet from "../images/GarudamonVpet.png";
import hououmonVpet2 from "../images/HououmonVpet2.png";
import rosa from "../images/Rosa.png";
import Card from "react-bootstrap/Card";
import ListGroup from "react-bootstrap/ListGroup";
import Carousel from "react-bootstrap/Carousel";

function Mirei() {
  return (
    <div className="exterior">
      <img src={fondo} className="fondo" />

      <Card bg="info" border="light" className="akiho">
        <Card.Img variant="top" src={akiho} />
        <Card.Body>
          <Card.Title>Akiho Rindou</Card.Title>
        </Card.Body>
        <ListGroup className="list-group-flush">
          <ListGroup.Item>Edad: 18</ListGroup.Item>
          <ListGroup.Item>digimon aliado: Piyomon</ListGroup.Item>
        </ListGroup>
        <Card.Body>
          <Card.Link href="/taiga"> carta</Card.Link>
          <Card.Link href="/mirei">next carta</Card.Link>
        </Card.Body>
      </Card>
      <div className="Informacion">
        <h1 className="InformacionAkiho">Informacion</h1>
        <img src={PiyomonVpet} className="PiyomonVpet" />
        <img src={PiyomonVpet} className="PiyomonVpet2" />

        <p classname="akihoText">
          Akiho Rindou, tambien conocida como Tamer Nyanko, es un personaje de
          los videojuegos Digimon World Re: Digitize y Digimon World Re:Digitize
          Decode, asi como de los mangas Digimon World Re:Digitize (manga) y
          Digimon World Re:Digitize Encode. Su companera es una Piyomon llamada
          Digitorin.
        </p>

        <p classname="akihoText2">
          Ella es la mejor jugadora del MMORPG Digital Monsters de GIGO durante
          los eventos del manga de Re:Digitize y al principio del juego.
        </p>

        <p classname="akihoText3">
          Niko parece estar enamorado de ella, pero no es correspondido. Siempre
          posee una naturaleza alegre, le gustan los digimon "hermosos", como
          los Digimon bebes, por lo que pasa mucho tiempo con ellos.
        </p>
      </div>
      <div className="fondoCarouselAkiho"></div>
      <div className="carouselDigimonAkiho">
        <Carousel className="carouselEstiloAkiho">
          <Carousel.Item>
            <img className="akihoImg1" src={akiho1} alt="First slide" />
            <Carousel.Caption></Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <img className="akihoImg2" src={akiho2} alt="Second slide" />
          </Carousel.Item>
          <Carousel.Item>
            <img className="akihoImg3" src={akiho3} alt="Third slide" />
            <Carousel.Caption></Carousel.Caption>
          </Carousel.Item>
        </Carousel>
      </div>

      <div className="DigimonAkiho">
        <h2>Digimon de Akiho</h2>
        <img src={rosa} className="rosaAkiho"></img>
        <img src={rosa} className="rosaAkiho2"></img>
      </div>

      <div className="piyomonVpetAkiho">
        <Card bg="light" border="info" className="card">
          <Card.Img variant="top" src={PiyomonVpet2} />
          <Card.Body>
            <Card.Title href="/taiga">Piyomon</Card.Title>
          </Card.Body>
          <ListGroup className="list-group-flush">
            <ListGroup.Item>Rookie</ListGroup.Item>
          </ListGroup>
        </Card>
      </div>
      <div>
        <Card bg="light" border="info" className="birdramonVpetAkiho">
          <Card.Img
            variant="top"
            src={BirdramonVpet}
            className="birdramonVpetAkihoImg"
          />
          <Card.Body>
            <Card.Title href="/taiga">Birdramon</Card.Title>
          </Card.Body>
          <ListGroup className="list-group-flush">
            <ListGroup.Item>Champion</ListGroup.Item>
          </ListGroup>
        </Card>
      </div>
      <div>
        <Card bg="light" border="info" className="garudamonVpetAkiho">
          <Card.Img
            variant="top"
            src={garudamonVpet}
            className="garudamonVpetAkihoImg"
          />
          <Card.Body>
            <Card.Title href="/taiga">Garudamon</Card.Title>
          </Card.Body>
          <ListGroup className="list-group-flush">
            <ListGroup.Item>Ultimate</ListGroup.Item>
          </ListGroup>
        </Card>
      </div>
      <div>
        <Card bg="light" border="info" className="hououmonVpetAkiho">
          <Card.Img
            variant="top"
            src={hououmonVpet2}
            className="hououmonVpetAkihoImg"
          />
          <Card.Body>
            <Card.Title href="/taiga">Houomon</Card.Title>
          </Card.Body>
          <ListGroup className="list-group-flush">
            <ListGroup.Item>Mega</ListGroup.Item>
          </ListGroup>
        </Card>
      </div>
    </div>
  );
}

export default Mirei;
