
import '../css/navbar.css';
import tentomon from '../images/logoDigimon2.gif'
import ArbolDetails from './ArbolDetails'
import React, { useState } from "react";
import { Route } from 'wouter'
import { Link } from 'wouter'
import { useLocation } from 'wouter'

function Navbar() {
  const [location, pushLocation] = useLocation()

  return (
    <nav className="navbar navbar-expand-lg bg-dark">
      <div className="container-fluid">
        <Link href='' onClick={() => pushLocation('/')}>
          <img src={ tentomon } className='logoNavbar' />
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarNav">
          <ul className="navbar-nav">
            <li className='home'>
              <Link href='' onClick={() => pushLocation('/')} >
              <button type='button' className='btn btn-outline-primary'>
                  inicio
              </button>
              </Link>
            </li>
            <li className="mapa">
              <Link href='' onClick={() => pushLocation('/arbol')}>
                <button type='button' className='btn btn-outline-info'>
                  mapa
                </button>
              </Link>
            </li>
            <li className="nav-item">
              <Link href='' onClick={() => pushLocation('/digimon')}>
                <button type='button' className='btn btn-outline-info'>
                  Digimon
                </button>
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
}

export default Navbar;
