
import { Link } from 'wouter'
import { useLocation } from 'wouter'
import "../css/carousel.css";
import Carousel from "react-bootstrap/Carousel";
import "../css/home.css";
import React from "react";
import fondo from "../images/DigimonReDigitize.jpg";
import taiga from "../images/taigadigimon.jpg";
import mirei from "../images/mirei.png";
import akinho from "../images/akhino.jpg";
import omegamonVpet from "../images/omegamonVpet.png";
import imperialdramonpmVpet from "../images/ImperialdramonpmVpet.png";
import lateral1 from "../images/lateral.jpg";
import "../css/navbar.css";

function Home() {
  const [location, pushLocation] = useLocation()

  return (
    <div className='home'>
      <img src={fondo} className="fondo" />
      <div className="container">
        <h1>
          Aqui encontraras casi toda la informacion al respecto de Digimon world
          Re:Digitize
        </h1>
      </div>
      <div className=" containerI">
        <div className="containerII">
          <p className="texto">
            Esta es una pagina donde podras encontrar una guia algo detallada al
            respecto de este juego. Esta pagina esta hecha con el fin de ayudar
            a los jugadores que no saben mucho sobre este juego, al igual que
            esta hecha sin fines de lucro y es una ayuda de un fan para otros
            fans, debido a que el juego solo se encuentra en japones y en
            ingles. Espero que esta pagina sea de su agrado y que les sirva de
            ayuda. Si algo esta mal en la pagina, o no agregue algo que sea
            importante o quieran dar sus recoemndaciones para ayudar en esta
            pagina pueden escribirme a mi correo: (introducir aqui el correo).
            Ahora no los entretengo mas con esta introduccion y les dejo con la
            pagina.
          </p >
        </div>
      </div>
      <div className="carousel">
        <Carousel>
          <Carousel.Item>
            <Link href='' onClick={() => pushLocation('/taiga')}>
            <img className="imagTaiga" src={taiga} alt="First slide" />
            </Link>
            <Carousel.Caption>
              <h3 className="titleTaiga">Taiga</h3>
              <p className="pTaiga">
                Personaje principal que usas durante la aventura del juego
              </p>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <Link href='' onClick={() => pushLocation('/mirei')}>
            <img className="imagMirei" src={mirei} alt="Second slide" />
            </Link>
            <Carousel.Caption>
              <h3 className="titleMirei">Mirei</h3>
              <p className="pMirei">
                Mirei al parecer tiene un desorden de personalida. Tiene como
                companera a dos Digimons: Angewomon y LadyDevimon
              </p>
            </Carousel.Caption>
          </Carousel.Item>
          <Carousel.Item>
            <Link href='' onClick={() => pushLocation('/akiho')}>
            <img className="imagAkiho" src={akinho} alt="Third slide" />
            </Link>
            <Carousel.Caption>
              <h3 className="titleAkiho">Akiho</h3>
              <p className="pAkiho">
                le gustan los digimon "hermosos", como los Digimon bebes, por lo
                que pasa mucho tiempo con ellos. Su companera es una Piyomon
                llamada Digitorin.
              </p>
            </Carousel.Caption>
          </Carousel.Item>
        </Carousel>
      </div>
      <div className="contenedorWiki">
        <h2 className="indice">Todo sobre la wiki</h2>
        <img className="omegamonVpetHome" src={omegamonVpet}></img>
        <img className="imperialdramonpmVpet" src={imperialdramonpmVpet}></img>
        <ul className="listadoDigimon"><Link href='/digimon' >Digimon obtenibles</Link></ul>
        <ul className="listadoPersonajes"><Link href='/personajes'>Personajes</Link></ul>
        <ul className="mapaDigimundo"><Link href='/arbol'>Mapa</Link></ul>
      </div>
    </div>
  );
}

export default Home;
