
import fondo from "../images/DigimonReDigitize.jpg";
import '../css/taiga.css';
import mirei from "../images/mirei.jpg";
import Card from "react-bootstrap/Card";
import ListGroup from "react-bootstrap/ListGroup";

function Mirei() {
  return (
    <div className="exterior">
        
        <img src={fondo} className="fondo" />

      <Card bg='secondary' border='info' className="card">
        <Card.Img variant="top" src={mirei} />
        <Card.Body>
          <Card.Title>Mirei</Card.Title>
        </Card.Body>
        <ListGroup className="list-group-flush">
          <ListGroup.Item bg='secondary' >hola</ListGroup.Item>
          <ListGroup.Item>Dapibus ac facilisis in</ListGroup.Item>
          <ListGroup.Item>Vestibulum at eros</ListGroup.Item>
        </ListGroup>
        <Card.Body>
          <Card.Link href="#">Card Link</Card.Link>
          <Card.Link href="#">Another Link</Card.Link>
        </Card.Body>
      </Card>
    </div>
  );
}

export default Mirei;
